<template>
    <el-form class="pubForm" :model="data" label-width="15%">
        <el-form-item label="Title"><el-input v-model="data.Title"></el-input></el-form-item>
        <el-form-item label="Price"><el-input v-model="data.Price" placeholder="Unit is DDD"></el-input></el-form-item>
        <el-form-item label="Keys"><el-input placeholder="Separate each tag with a comma or semicolon"
                                             v-model="data.Keys" type="textarea" :rows=2></el-input></el-form-item>
        <el-form-item label="Description">
            <el-input v-model="data.Description" type="textarea" :rows=3></el-input></el-form-item>
        <el-form-item label="Data"><el-input ref="seldata" @change="selFile" type="file"></el-input></el-form-item>
        <el-form-item label="Proofs"><el-input ref="selproof" @change="selFiles" type="file" multiple></el-input></el-form-item>
        <el-form-item>
            <el-button type="primary" @click="pub">Publish</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
import {dl_db} from "../../options";
export default {
    name: "PublishNewData",
    data () {
        return {
            data: {
                Title: '',
                Price: '',
                Keys: '',
                Description: '',
                Data: '',
                Proofs: []
            },
        }
    },
    methods: {
        selFile: function () {
            let dp = this.$refs.seldata
            let dpf = dp.$refs.input.files[0]
            let reader = new FileReader()
            let _this = this
            reader.readAsDataURL(dpf)
            reader.onload = function (evt) {
                _this.data.Data = evt.target.result
            }
        },
        selFiles: function () {
            let pp = this.$refs.selproof
            let ppf = pp.$refs.input.files
            let _this = this
            for (let i=0;i<ppf.length;i++) {
                let reader = new FileReader()
                reader.readAsDataURL(ppf[i])
                reader.onload = function (evt) {
                    _this.data.Proofs.push(evt.target.result)
                }
            }
        },
        pub: function () {
            let _this = this
            let pub = {}
            pub.Title = this.data.Title; pub.Price = this.data.Price; pub.Keys = this.data.Keys
            pub.Description = this.data.Description; pub.Owner = this.$store.state.account
            astilectron.sendMessage({Name:"publish",Payload:this.data}, function (message) {
                if (message.payload) {
                    let ID = "Qm462" // interface call will return an unique id also,remember to modify it.
                    dl_db.write(pub, ID)
                    dl_db.init(_this)
                }else {
                    alert("Publish data failed.")
                }
            })
            // this.resetData()
        }
    }
}
</script>

<style scoped>
.pubForm {
    padding: 0 20% 0 10%;
}
</style>



